.footers {
  max-width: 200px;
  margin-left: auto;
  margin-right: auto;
}

.footers > h1 {
  text-align: center;
  color: white;
}

.footers__link {
  list-style-type: none;
  left: 10px;
  width: 100%;
  height: 25px;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  font-size: 15px;
  text-align: center;
}

.footers__link > a {
  cursor: pointer;
  color: #f1f1f1;
  text-decoration: none;
}
