.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__contents{
    display: flex;
    justify-content: space-between;
}
.nav__black{
    background-color: black;
}
.nav__logo {
    position: fixed;
    top: 10px;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}
.nav__avatar {
    cursor: pointer;
    position: fixed;
    right: 40px;
    width: 70px;
    top: 10px;
}

.list {
  list-style-type: none;
  left: 0;
  width: 100%;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
}
.items {
  margin-right: 20px;
  font-size: 15px;
  /* text-transform: uppercase; */
}

.items > a {
  cursor: pointer;
  color: #f1f1f1;
  text-decoration: none;
}

.items > a:hover {
  color: #e50914;
}

.cursor {
  margin: 20px;
}

.button__search {
  height: 25px;
  width: 32px;
  background: lightgray;
  border: none;
  border-radius: 5px;
}

.input__search {
  height: 18px;
  font-size: 15px;
  border: none;
  border-radius: 4px;
}

.btn {
  display: none;
  position: fixed;
  top: 0;
  left: 10px;
  color: #000;
  font-size: 18px;
}

@media screen and (max-width: 500px) {
  .list {
    flex-direction: column;
    background-color: #000;
    width: auto;
    opacity: 0.8;
    height: auto;
    margin-left: 10px;
    border-radius: 10px;
  }
  .items:nth-child(1) {
    margin-top: 50px;
  }
  .items {
    padding: 20px 0;
  }
  .btn {
    display: block;
    top: 20px;
    left: 10px;
  }
}
