.rowview {
  color: white;
  margin-left: 20px;
}
.rowview__posters {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  align-content: space-between;
}

.card {
  padding: 10px;
  width: 10%;
  margin-right: 20px;
  transition: transform 450ms;
}

.card:hover {
  transform: scale(1.09);
  opacity: 1;
}

.card > img {
  max-height: 250px;
}

.card > h3 {
  text-align: center;
}

.card > p {
    font-size: 13px;
	color: #ffffff;
	background: #e53637;
	display: inline-block;
	padding: 2px 12px;
	border-radius: 4px;
	position: absolute;
}

@media only screen and (max-width: 479px) {
  .card {
    width: 36%;
    margin-left: 0px;
    left: 10px;
  }

  .card > img {
    height: 220px;
    width: 140px;
  }

  .card > h3 {
    margin: 10px;
    font-size: 15px;
  }
}

