/* Tablet Device = 768px */

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .card {
    width: 14%;
    margin-right: 60px;
    padding: 5px;
  }

  .card > img {
    height: 220px;
    width: 130px;
  }

  .card > h3 {
    margin: 5px;
  }
}

/* Medium Device = 1200px */

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .card {
    width: 12%;
    margin-right: 60px;
    padding: 5px;
  }

  .card > img {
    max-height: 250px;
    width: 150px;
  }
}


@media only screen and (min-width: 1200px) {
  .card {
    width: 9%;
    margin-right: 10px;
    padding-left: 35px;
  }

  .card > img {
    max-height: 240px;
    width: 150px;
  }

  .card > h3 {
    margin: 5%;
  }
}