.watching {
    color: white;
}

.Viewfilms___body {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    padding: 5%;
    max-width: 800px;
}

.viewfilms__wacth > Iframe {
    width: 100%;
    height: 700px;
    max-width: 800px;
}

.viewfilms__wacth > button {
    position: absolute;
    top: 50%;
    right: 50%;
}

@media screen and (max-width: 500px) {
    .watching {
        height: 100vh;
        color: white;
    }
    .Viewfilms___body {
       width: 60%;
       top: 10px;
       margin-left: 10px;
       padding-top: 20%;
    }
    .viewfilms__wacth > Iframe {
        width: 140%;
        height: 400px;
    }

    .viewfilms__wacth > h1 {
        font-size: medium;
    } 
  }