.searchvalue {
  color: white;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  margin-left: 25px;
}

.Nofaould {
    height: 100vh;
}

.searchvalue__posters {
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  align-content: space-between;
}

.searchvalue__poster {
  padding: 10px;
  width: 10%;
  margin-right: 20px;
  transition: transform 450ms;
}

.searchvalue__poster:hover {
  transform: scale(1.09);
  opacity: 1;
}

.searchvalue__poster > img {
  max-height: 250px;
}

.searchvalue__poster > h3 {
  text-align: center;
}

.searchvalue__poster > p {
  font-size: 13px;
  color: #ffffff;
  background: #e53637;
  display: inline-block;
  padding: 2px 12px;
  border-radius: 4px;
  position: absolute;
}

@media only screen and (max-width: 479px) {
  .searchvalue__poster {
    width: 36%;
    margin-left: 2px;
    left: 10px;
  }

  .searchvalue__poster > img {
    height: 220px;
    width: 140px;
  }

  .searchvalue__poster > h3 {
    margin: 10px;
    font-size: 15px;
  }
}

