/* Tablet Device = 768px */

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .searchvalue__poster {
    width: 14%;
    margin-right: 60px;
    padding: 5px;
  }

  .searchvalue__poster > img {
    height: 220px;
    width: 130px;
  }

  .searchvalue__poster > h3 {
    margin: 5px;
  }
}

/* Medium Device = 1200px */

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .searchvalue__poster {
    width: 12%;
    margin-right: 60px;
    padding: 5px;
  }

  .searchvalue__poster > img {
    max-height: 250px;
    width: 150px;
  }
}

@media only screen and (min-width: 1200px) {
  .searchvalue__poster {
    width: 9%;
    margin-right: 10px;
    padding-left: 35px;
  }

  .searchvalue__poster > img {
    max-height: 240px;
    width: 150px;
  }

  .searchvalue__poster > h3 {
    margin: 5%;
  }
}
