.row {
    color: white;
    margin-left: 20px;
}
.row__posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.span {
    display: flex;
}

.span > span {
    margin-left: 10px;
    margin-top: 8px;
    cursor: pointer;
}

.row__posters::-webkit-scrollbar{
    display: none;
}

.cards {
    object-fit: contain;
    margin-right: 20px;
    width: 100%;
    transition: transform 450ms;
}

.cards > p {
    font-size: 13px;
	color: #ffffff;
	background: red;
	padding: 2px 12px;
	border-radius: 4px;
}

.cards:hover {
    transform: scale(1.09);
    opacity: 1;
}

.cards > img {
    max-height: 250px;
}

.cards > h3 {
    text-align: center;
}



